body {
  background: #131b23;
  margin: 2px 30px 0 20px;
}

header {
  padding: 2rem 0;
  position: fixed;
  top: 0;
  left: 0;
}

a {
  color: #fff;
}

.product {
  background: #ffffff31;
  border-radius: 10px;
  align-items: center;
  margin: 150px 70px 0;
  display: flex;
}

.product > div {
  width: 50%;
  height: 45%;
}

.product__mainImage {
  width: 100%;
  height: 180px;
  object-fit: contain;
}

.product__galllery img {
  width: 80px;
  cursor: pointer;
}

.product__cart {
  color: #fff;
  text-align: center;
  width: 300px;
  height: 38px;
  cursor: pointer;
  background: #f64d33;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  margin-bottom: 30px;
  font-weight: 200;
  box-shadow: 0 2px 4px #0003;
}

.product__cart:hover {
  background: #fa614a;
}

.product__description {
  color: #fff;
  margin-top: 10px;
  margin-left: 4px;
  font-size: 15px;
}

.product__name {
  margin-top: 30px;
  font-size: 35px;
  font-weight: bold;
}

.product__category {
  color: #fff;
  margin-top: 15px;
  margin-left: 4px;
  font-size: 20px;
  font-weight: 500;
}

.product__price {
  font-size: 40px;
  font-weight: medium;
  justify-items: center;
  margin-top: 10px;
}

@media (max-width: 375px) {
  body {
    justify-content: center;
    margin: 0;
    padding: 0;
    display: flex;
  }

  .product {
    flex-direction: column;
    margin-top: 110px;
    padding: 20px;
  }

  .product > div {
    width: 100%;
    height: 100%;
  }

  .product__name {
    width: 85%;
    font-size: 1.7em;
    font-weight: 600;
  }

  .product__description {
    width: 85%;
    font-size: .9em;
    font-weight: 100;
  }

  .product__price {
    font-size: 1.2em;
    font-weight: 600;
  }

  .product__cart {
    width: 270px;
  }

  .product__gallery {
    margin-right: 5px;
  }

  .product__gallery img {
    width: 90px;
    cursor: pointer;
    margin-bottom: 15px;
  }

  .logo a {
    position: absolute;
    top: 10px;
    left: 40px;
  }
}

@media (max-width: 420px) {
  body {
    justify-content: center;
    margin: 0;
    padding: 0;
    display: flex;
  }

  .product {
    flex-direction: column;
    margin-top: 150px;
    padding: 40px 20px 20px;
  }

  .product > div {
    width: 100%;
    height: 100%;
  }

  .product__name {
    width: 85%;
    font-size: 1.7em;
    font-weight: 600;
  }

  .product__description {
    width: 85%;
    margin-top: 20px;
    font-size: 1em;
    font-weight: 100;
  }

  .product__price {
    font-size: 1.2em;
    font-weight: 600;
  }

  .product__cart {
    width: 250px;
  }

  .logo a {
    position: absolute;
    top: 10px;
    left: 40px;
  }
}

